import React from "react";

export default function Total(props) {
  return (
    <div>
      <div className="amount">
        <div>
          <p className="first">Tip Amount</p>
          <p className="second">/person</p>
        </div>
        <p className="third">${parseInt(props.tip) / parseInt(props.people)}</p>
      </div>
      <div className="amount">
        <div>
          <p className="first">Total</p>
          <p className="second">/person</p>
        </div>
        <p className="third">
          ${parseInt(props.total) / parseInt(props.people)}
        </p>
      </div>
    </div>
  );
}
