import React from "react";
import people from "../images/icon-person.svg";

class People extends React.Component {
  render() {
    const handelChange = (e) => {
      this.props.setPeople(e.target.value);
      console.log(e.target.value);
    };
    return (
      <div className=" mb-3 mt-3">
        <label for="bill" className="form-label">
          Number of People
        </label>
        <div className="input-group ">
          <span className="input-group-text " id="basic-addon1">
            <img src={people} className="img-responsive" />
          </span>
          <input
            style={{ direction: "rtl" }}
            value={this.props.people}
            type="text"
            className="form-control border-start-0"
            id="bill"
            placeholder={this.props.people}
            onChange={handelChange}
          ></input>
        </div>
      </div>
    );
  }
}
export default People;
