import React from "react";

export default function Reset(props) {
  const handleClick = () => {
    props.setPeople(1);
    props.setTip(0);
    props.setTotal(0);
    props.setBill(0);
    props.setTipp(0);
  };
  return (
    <div>
      <button className="reset" onClick={handleClick}>
        Reset
      </button>
    </div>
  );
}
