import React, { Component } from "react";

export default class Tip extends Component {
  render() {
    const handleTipp = (e) => {
      this.props.setTipp(e.target.value);
      handleTip(e);
    };
    const handleTip = (e) => {
      this.props.setTip((this.props.bill * e.target.value) / 100);
      this.props.setTotal(parseInt(this.props.bill) + parseInt(this.props.tip));
      console.log(this.props.total);
    };

    return (
      <div className="tips">
        <label className="col-12 mb-3">Select Tip</label>

        <button className="col-3 me-2 mb-2  " value={5} onClick={handleTip}>
          5%
        </button>

        <button className="col-3 me-2 mb-2 " value={10} onClick={handleTip}>
          10%
        </button>

        <button className="col-3 me-2 mb-2 " value={15} onClick={handleTip}>
          15%
        </button>

        <button className="col-3 me-2 mb-2 " value={25} onClick={handleTip}>
          25%
        </button>

        <button className="col-3 me-2 mb-2 " value={50} onClick={handleTip}>
          50%
        </button>

        <input
          className="col-3 me-2 mb-2"
          placeholder="Custom"
          onChange={handleTipp}
          value={this.props.tipp}
          style={{ height: "56px", paddingLeft: "30px" }}
        ></input>
      </div>
    );
  }
}
