import React from "react";
import Bill from "./comps/Bill";
import "./App.css";
import Tip from "./comps/Tip";
import People from "./comps/People";
import Total from "./comps/Total";

import Reset from "./comps/Reset";
const App = () => {
  const [tipp, setTipp] = React.useState(0);
  const [bill, setBill] = React.useState(0);
  const [people, setPeople] = React.useState(1);
  const [tip, setTip] = React.useState(0);
  const [total, setTotal] = React.useState(0);
  return (
    <div className="container ">
      <div className="row">
        <div className="col-sm-6">
          <div className="row">
            <Bill className="col-12" setBill={setBill} bill={bill}></Bill>
            <Tip
              className="col-12"
              setTip={setTip}
              setTotal={setTotal}
              total={total}
              bill={bill}
              tip={tip}
              setTipp={setTipp}
              tipp={tipp}
            ></Tip>
            <People setPeople={setPeople} people={people}></People>
          </div>
        </div>
        <div
          className="col-sm-6"
          style={{
            background: "hsl(183, 100%, 15%)",
            borderRadius: "15px",
            padding: "20px",
          }}
        >
          <Total total={total} people={people} tip={tip}></Total>
          <Reset
            setBill={setBill}
            setPeople={setPeople}
            setTip={setTip}
            setTotal={setTotal}
            setTipp={setTipp}
          ></Reset>
        </div>
      </div>
    </div>
  );
};

export default App;
